.motor{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family:sans-serif;
    width: 100%;
      .item{
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        font-family:sans-serif;
     background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.4),rgba(0,0,0,0.6)),
     url(./mechanic.jpg);
     background-position: center;
     background-size: cover;
     background-repeat:no-repeat;
     top: 0;
     right: 0;
     bottom: 0;
     left: 0;
    width: 100%;
    padding-bottom:40px;
    min-height: 60vh;
    max-height:auto;  
    h1{
        margin-top: 10px;
        color: gold;
        font-family:sans-serif;
        
    }
    h2{
        margin-top: 10px;
        font-size: 23px;
        width: 800px;
        color: white;
        
    }
     .body{
         
         display: flex;
         flex-wrap: wrap;
         justify-content: center;
         align-items: center;
         font-family:sans-serif;
     }
     p{
        font-family:sans-serif; 
    color: white;
        margin:20px;
        max-width: 70vh;
        min-width: auto;
         h3{
             color: gold;
             margin-bottom: 0px;
         }

     }
     .factors{
         margin-top: 10px;
         b{
             color: gold;
             font-size: 20px;
             margin-bottom: 3px;
         }
     }
    }
    #h2{
        margin-top: 30px;
        font-size: 16px;
        width:450px;
        height:auto;
       color:white;
        font-family: sans-serif;

     }
     .im1{
        display: flex;
        align-items: center;
        justify-content: center;
        background: whitesmoke;
       margin-bottom: 20px;
       margin-top: 20px;
        width: 100%;
        border-bottom-right-radius:10%;
        border-top-right-radius:20% ;
        .examp2{
        
           width: 70%;
           margin: 20px;
            display: grid;
            grid-template-columns:repeat(auto-fit,minmax(150px,1fr));
            grid-row:repeat(3,1fr);
            grid-gap:5px;
            img{
    height: 200px;
    width: 180px;
    border:hidden;
    box-shadow: 0px 0px 6px rgba(0,0,0,0.4);
    border-top-right-radius:30px;
    margin: 10px;
    background-color:white;
    align-self: center;
    &:hover{
        transform: translateY(7px);
    }
            }
        }
    }
    
}