
.hire{
    width: 100%;
    font-family:sans-serif;
    
h1{
    
    color: gold;
    font-family:sans-serif;
}
.pur{ 
    *{
        margin-top: 10px;
    }
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
    width: 100%;
    color: white;
    background:linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.7),rgba(0,0,0,0.7)),
    url(./hire.png);
    background-position: center;
    background-size: cover;
    background-repeat:no-repeat;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding-bottom:20px;
    min-height: 60vh;
    max-height:auto;
#hire{
        min-width: auto;
        max-width: 120vh;
        font-size: 20px;
        margin: 10px;
        font-family:sans-serif;
    }
    .down{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        font-family:sans-serif;
        ul{
          
            b{
                color: gold;
            }
            li{
                margin: 0;
            }
           
        }
        
        #p{
            margin-left: 80px;
            max-width: 400px;
            min-width: auto;
            font-family:sans-serif;
            p{
                margin-top: 0;
            }
        }
      
    } 
    i{
        font-size: 30px;
        
    }   
    }
    .im{
        margin-top: 10vh;
        margin-bottom: 5vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgb(22, 27, 68);
        width: 100%;
        border-bottom-left-radius:20%;
            border-top-right-radius:20%;
.examp{
           width: 70%;
           margin: 20px;
            display: grid;
            grid-template-columns:repeat(auto-fit,minmax(120px,1fr));
            grid-row:repeat(3,1fr);
            grid-gap: 10px;
            //transform:skewX(20deg);
            
    img{
        height: 160px;
        width:150px;
       border:hidden;
       box-shadow: 0px 0px 7px whitesmoke;
        margin: 10px;
        background-color:white;
        align-self: center;
    &:hover{
        transform: translateY(7px);
    }
            }
        }
    }
   



}