*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
.App{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family:sans-serif;
    
    .up{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
       background:linear-gradient(to bottom,whitesmoke,white);
        color: white;
        width: 100%;  
    h1{
        font-size:23px;
        position: relative;
      max-width: 950px;
      min-width: auto;
    margin: 20px;
    font-family: sans-serif;
    color:rgba(0,0,0,2);
    box-shadow: 2px 2px 2px rgba(0,0,0,0.2);
    }
    .link{
        display:flex;
        flex-wrap: wrap;
        margin-top:10px;
        margin-bottom: 3px;
        flex-flow: start;
        color: blue;
    
        *{  
        text-decoration: none;
        width: 140px;
        height: 15px;
        margin-bottom: 5px;
        color: blue;
        &:hover{
            box-shadow: 2px 2px 2px rgba(9, 118, 138, 0.364);
        }
        }
    }
   .img{
       z-index: 2;
       height: 90px;
       width: 100%;
       animation-name: anim;
       animation-duration: 30s;
       animation-delay: 2s;
       animation-iteration-count: infinite;
       @keyframes anim {
        0%{ background:url(./weave.jpg);background-position:center;background-size: cover;}
        25%{ background:url(./motor.png);background-position:center;background-color: white;}
        50%{ background:url(./hire.png);background-position:center;background-color: white;}
        75%{ background:url(./motor.png);background-position:center;background-color: white;}
        100%{ background:url(./weave.jpg);background-position:center;}  
       }
   } 
   
    }
    
}